@tailwind base;
@tailwind components;
@tailwind utilities;
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&family=Karantina&family=Montserrat:wght@400;700&display=swap");
@font-face {
  font-family: "TildaSans-VF";
  src: local("TildaSans-VF"),
    url("./Fonts/TildaSans-VF/TildaSans-VF.ttf") format("truetype");
}

body {
  margin: 0;
  background-color: #282c33;
  font-family: "TildaSans-VF", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
